:root {
    --font-primary: 'Roboto', sans-serif;
    --font-secondary: 'Oswald', sans-serif;
    --color1: #222732;
    --color2: #313847;
    --color3: #d6d6d6;
    --color3-1: rgb(238, 238, 238);
    --color4: #eee;
    --colorp: #ff7d44;

    // --wrapper-width: 1116px;
    --wrapper-width: 1200px;
    --header-height: 70px;
    --header-height-notactive: 120px;
    --nav-margin-item: 20px;
    --hero-slider-height: 70vh;
    --catalog-item-num: 3;
    --catalog-item-minh: 100px;
    // --hero-slider-height:100vh;

}


html {
    background: #fff;
    -webkit-text-size-adjust: 100%;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-rendering: optimizeLegibility;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    // backface-visibility: hidden;

}

ul,
li,
ol {
    list-style: none;
    margin: 0;
    padding: 0;
}

a,
a:hover {
    text-decoration: none;
}


.a {
    visibility: hidden;
    opacity: 0;

    &.uk-scrollspy-inview {
        visibility: visible;
        opacity: 1;
    }
}


.checkbox,
.radio {
    margin: 5px 0;
    display: flex;
    align-items: center;

    >input {
        margin-right: 10px;
        margin-top: 0;

        &:checked,
        &:focus:checked,
        &:active:checked {
            background-color: var(--color1);
        }
    }

    .legend {
        font: 500 14px/100% var(--font-primary);
        letter-spacing: 2%;
        color: var(--color1);
    }

    &.ttu {
        .legend {
            letter-spacing: 0.5px;
            text-transform: uppercase;
            font-size: 12px;
        }
    }
}

.button {
    border-radius: 30px;
    width: auto;
    text-align: center;
    padding: 0 30px;
    height: 42px;
    display: inline-flex;
    text-transform: uppercase;

    &.button-default {

        width: auto;
        border: solid 2px #222;
        background-color: #222;
        cursor: pointer;
        font-size: 14px;
        font-weight: 500;
        line-height: 37px;
        overflow: hidden;
        color: #fff;
        text-align: center;
        transition: border 0.2s ease-out;

    }
}

.breadcrumb {
    li {
        margin-right: 10px;
        color: var(--color1);

        a {
            font: normal 15px/100% var(--font-primary);
            color: inherit;
            text-transform: uppercase;
        }

        &:before {
            margin-right: 10px;
            content: '/';
            color: inherit;
        }

        &:first-child {
            &:before {
                display: none;
            }
        }
    }

}



body {

    font: 400 16px/130% var(--font-primary);
    color: black;
    // -webkit-font-smoothing: antialiased;
    // -moz-osx-font-smoothing: grayscale;
    // min-height: 100vh;
    // &::after {
    //     content: 'xs'\ -xs;
    //     content: 'sm'\ -sm;
    //     content: 'md'\ -md;
    //     content: 'xl'\ -xl;
    //     display: none !important;
    // }
    opacity: 0;

    &.loaded {
        opacity: 1;
        transition: opacity .5s ease-in-out 0s;
    }

}


.wrapper {
    margin-left: auto;
    margin-right: auto;
    max-width: var(--wrapper-width);
    width: 100%;

    &.wide {
        max-width: 100%;
        width: auto;
        margin-left: 70px;
        margin-right: 70px;
    }


}

.reveal-text {
    visibility: hidden;
}

.line-text {
    position: relative;
    overflow: hidden;
    display: flex;
}

h2.global-title {

    font: 400 4vw/120% var(--font-secondary);
    text-transform: uppercase;
    overflow: hidden;
}




.text-line {
    overflow: hidden;

}

[data-aos="reveal-animation"] {
    >.i {
        transform: translateY(100%);
    }

    &:not(.aos-animate) {
        .i {

            animation: reveal-reverse 1.2s ease;
            animation-fill-mode: forwards;
        }
    }

    &.aos-animate {
        .i {
            animation: reveal .8s ease;
            animation-fill-mode: forwards;
        }
    }
}

@keyframes reveal {
    from {
        transform: translateY(100%);
    }

    to {
        transform: translateY(0%);
    }
}

@keyframes reveal-reverse {
    from {
        transform: translateY(0%);
    }

    to {
        transform: translateY(100%);
    }
}

