#grid {
    padding: 120px 0;

    .portfolio-wrap {
        position: relative;
        margin: 0 auto;
        width: 100%;
        padding: 0;
        box-sizing: border-box;
        min-height: 10vh;

        &.ladder-grid {
            padding: 20px 40px;
        }
    }

    .portfolio {
        position: relative;
        margin: 0 auto;
        box-sizing: border-box;
        transition: height 0.5s ease-out;

        &:after {
            content: "";
            display: table;
            clear: both;
        }

        .item-image {
            cursor: pointer;
        }
    }

    .item {
        display: block;
        position: relative;
        box-sizing: border-box;
        z-index: 0;
        margin: 80px 80px;
        width: calc(50% - 160px);
        height: 28vw;
        z-index: 0;
        pointer-events: none;

        &::after {
            content: "";
            display: block;
            padding-bottom: 100%;
        }

        &.hover {
            z-index: 1;
        }

        &.active {
            .item-caption {
                opacity: 1;
            }
        }
    }

    .flex-grid {
        padding: 0 70px;
        box-sizing: border-box;

        .portfolio {
            width: 100%;
            display: flex;
            grid-area: list;
            margin: 20px 0;
            box-sizing: border-box;

            &:hover {
                .item {
                    .item-title {
                        opacity: 0;
                    }

                    &:hover {
                        .item-title {
                            opacity: 1;
                        }
                    }
                }
            }
        }

        .item {
            width: calc(3 * calc(1/10 * 100%));
            // height: 32vw;
            height: 23vw;
            display: block;
            position: relative !important;
            left: 0 !important;
            top: 0 !important;
            margin: 0px 10px;
            transition: width 0.6s cubic-bezier(0.38, 0.005, 0.215, 1);

            &:hover {
                width: calc(8 * calc(1/10 * 100%));

                .item-cat {
                    opacity: 0.4;
                    -webkit-transition: all 0.2s ease-out 0.2s;
                    transition: all 0.2s ease-out 0.2s;
                }
            }

            .item-title {
                opacity: 1;
                -webkit-transition: all 0.2s ease-out 0s;
                transition: all 0.2s ease-out 0s;
            }

            .item-cat {
                opacity: 0;
                -webkit-transition: all 0.2s ease-out 0s;
                transition: all 0.2s ease-out 0s;
            }
        }
    }


    .item-parallax {
        height: 100%;
        width: 100%;
        position: absolute;
        pointer-events: initial;
    }

    .item-appear {
        display: block;
        position: relative;
        box-sizing: border-box;
        z-index: 0;
        width: 100%;
        height: 100%;
        overflow: hidden;
    }

    .item-image {
        width: 100%;
        height: 100%;
        position: relative;
        display: block;
        z-index: 100;
        object-position: center;
        object-fit: cover;

        &::after {
            content: "";
            opacity: 1;
            width: 100%;
            height: 60%;
            position: absolute;
            bottom: 0;
            left: 0;
            pointer-events: none;
            background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.5) 100%);
            filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00000000', endColorstr='#a6000000', GradientType=0);
            transition: all 0.6s ease-in-out;
        }
    }

    .item-content {
        width: 100%;
        height: 100%;
        position: relative;
        opacity: 1;
    }

    .fade-scaleout-effect {
        .item-content {
            opacity: 1;
            transform: scale(1.2);
            transition: all 1s cubic-bezier(.165, .84, .44, 1) 0.2s;
        }
    }

    .fade-scaleout-effect .active .item-content,
    .shortcode-portfolio .item-content {
        opacity: 1;
        -webkit-transform: scale(1);
        transform: scale(1);
        position: absolute;
        right: 0;
    }

    .item-wrap {
        width: 100%;
        height: 100%;
        position: absolute;
    }

    .item-wrap-image {
        width: 100%;
        height: 100%;
        position: relative;
        z-index: 0;
        overflow: hidden;
        -webkit-transform: scale(1.05);
        transform: scale(1.05);
        transition: all 0.6s cubic-bezier(0.38, 0.005, 0.215, 1);
    }

    .item-parallax:hover .item-appear .item-wrap-image,
    .above .item-wrap-image {
        -webkit-transform: scale(1);
        transform: scale(1);
    }

    .disable-ajaxload {
        .item-wrap-image {
            pointer-events: none;
        }
    }

    .above {
        &.item {
            opacity: 1;
        }
    }

    .scale-fitthumbs {
        .above {
            &.item {
                opacity: 1 !important;
            }
        }
    }

    .item-caption {
        z-index: 10;
        position: absolute;
        top: 100%;
        height: auto;
        padding: 25px 0;
        box-sizing: border-box;
        width: 100%;
        cursor: default;
        pointer-events: none;
        opacity: 1;
        transition: all 0.2s ease-out 0.4s;
    }

    .item-title {
        display: block;
        width: auto;
        margin: 0;
        font:500 18px/26px var(--font-secondary);
        text-transform: uppercase;
        // font-weight: 500;
        // font-size: 18px;
        // line-height: 26px;
        position: relative;
        color: #222;
        float: left;

        span {
            display: block;
            margin: 0;
            min-width: 10px;
            width: auto;
            box-sizing: border-box;
        }
    }

    .uppercase-text {
        .item-title {
            text-transform: uppercase;
            line-height: 20px;
        }

        .item-cat {
            text-transform: uppercase;
            line-height: 20px;
        }
    }

    .item-cat {
        display: block;
        position: relative;
        width: auto;
        font-size: 14px;
        line-height: 26px;
        overflow: hidden;
        color: #222;
        opacity: 0.4;
        float: right;

        span {
            display: inline-block;
            margin: 0;
            min-width: 10px;
            width: auto;
            box-sizing: border-box;
            transition: transform 0.2s;
            transform-origin: 100% 0%;

            &::before {
                position: absolute;
                top: 100%;
                width: 100%;
                left: 0;
                content: attr(data-hover);
                text-align: right;
            }
        }
    }

    .light-content .item-cat,
    .dark-section .item-cat {
        color: #fff;
    }

    .item:hover .item-cat span,
    .item.above .item-cat span {
        -webkit-transform: translateY(-100%);
        transform: translateY(-100%);
        transform-origin: 0% 0%;
    }

    .flex-grid .item:hover .item-cat span,
    .item.above .item-cat span {
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }

}

